@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700,900&display=swap);
:root {
  --bg: #FFF;
  --fg: #2f3542
}


body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #FFF;
  background: var(--bg);
  position: relative;
  min-height: 100vh;
}


.App{
  max-width: 70% !important;
  margin: auto;
  position: relative;
  height: 100%;
}

.App-Wrapper{
  min-height: 100vh;
  width: 100vw;
  height: auto;
}

.theme-mode-container{
  position: absolute;
  top: 20px;
  right: 30px;
  display: flex;
  justify-content: center;
}

#unit{
  font-size: 30px !important;
  font-weight: bold;

}

.current-container .current-title{
  font-size: 28px;
  font-weight: bold;
  margin-left: 20px;
  color: #2f3542;
  color: var(--fg)
}
.current-container .current-temp{
  font-size: 7.5em;
  font-weight: bolder;
  text-align: center;
  color: #2f3542;
  color: var(--fg);
  margin-bottom: 6px;
}


.ant-tabs-nav.ant-tabs-nav-animated{
  font-size: large;
}


.ant-card-head-title{
  font-size: 20px;
  font-family: cursive, 'Nunito';
}


.ant-alert{
  margin: 0px;
}
.ant-card-head{
  border: none;
}

.card-description{
  font-size: 35px;
  text-align: center;
  font-weight: bolder;
}

.ant-alert-message{
  font-size: 23px !important;
  color: #dddbdbab;
}
.ant-alert-description{
  font-size: 25px !important;
  font-weight: bold;
  padding: 10px;
  line-height: 1.5;
}

.ant-card.ant-card-bordered.ant-card-hoverable:hover{
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

/*  */
.card-container > .ant-tabs-card > .ant-tabs-content {
  height: auto;
  margin-top: -16px;
}

.card-container > .ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane {
  background: #fff;
  padding: 16px;
}

.card-container > .ant-tabs-card > .ant-tabs-bar {
  border-color: #fff;
}

.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
  border-color: transparent;
  background: transparent;
}

.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  border-color: #fff;
  background: #fff;
}

.ant-alert.ant-alert-info.ant-alert-with-description{
  background-color: #FFF !important;
}


@media only screen and (max-width: 1100px) {

  .App{
    min-width: 90% !important;
    margin: auto;
  }

  #unit{
    font-size: 22px !important;
  }
  .ant-tabs-nav.ant-tabs-nav-animated{
    font-size: medium;
  }

  .ant-card-head-title{
    font-size: 15px;
  }
  .ant-card-body h1{
    font-size: 26px;
  }
  .ant-alert-description{
    font-size: 14px !important;
    padding: 0;
  }
}

@media only screen and (max-width: 730px) {
  #unit{
    font-size: 20px !important;
  }
  .ant-alert.ant-alert-info.ant-alert-with-description{
    padding: 10px;
  }
  .ant-alert-message{
    font-size: 16px !important;
  }
  .anticon.anticon-info-circle.ant-alert-icon{
    display: none;
  }

.ant-alert-message{
  font-size: 12px !important;
}
  .ant-card-head-title{
    font-size: 13px;
  }
  .ant-card-body h1{
    font-size: 22px;
  }
  .ant-alert-description{
    font-size: 10px !important;
  }
  .ant-tabs-nav.ant-tabs-nav-animated{
    font-size: small;
  }

}

@media only screen and (max-width: 600px){

  .App{
    min-width: 100%;
  }

  #unit{
    font-size: 17px !important;
  }

  .theme-mode-container{
    position: absolute;
    top: 10px;
    right: 0px;
    display: flex;
    justify-content: center;
  }

  .ant-tabs-nav.ant-tabs-nav-animated{
    font-size: smaller;
  }
  .ant-card-body{
    padding: 0;
  }
  .current-temp{
    font-size: 35px !important;
  }
  .current-title{
    font-size: 20px !important;
  }
  .ant-card-head-title{
    font-size: 12px !important;
  }
  .ant-card-body h1{
    font-size: 14px !important;
  }
  .ant-alert-description{
    font-size: 10px !important;
  }
}
